import {
	mapState,
	mapMutations
} from 'vuex'
export default {
	data() {
		return {

			phoneNumber: '',
			code: '',
			recommendCode: '', //推荐码
			getCodeText: '获取验证码',
			getCodeBtnColor: '#ffffff',
			getCodeisWaiting: false,
			Code: ''
		};
	},
	onLoad(options) {
		let token = uni.getStorageSync('CODE')
		let userId = uni.getStorageSync('userId')
		this.openId = options.openId
		console.log(uni.getStorageSync('CODE'), this.$store.state, token != '' && userId != '', '555');
		if (token != '' && userId != '') { //登陸了就跳轉
			uni.switchTab({
				url: '/pages/tabbar/index/index'
			})
		} else if (token != '') {
			uni.navigateTo({
				url: '/pages/loginBase/chooseIdentity/chooseIdentity'
			})
		}

	},
	methods: {
		...mapMutations(['login']),

		cancelLogin() {
			uni.reLaunch({
				url: '/pages/index/index'
			})
		},
		// 获取经纬度
		getLocation() {
			uni.getLocation({
				type: 'wgs84',
				success: (res) => {
					this.$store.state.longitude = res.longitude
					this.$store.state.latitude = res.latitude
					console.log('当前位置的经度：' + res.longitude);
					console.log('当前位置的纬度：' + res.latitude);
				}
			});
		},
		// 获取验证码
		getCode() {
			uni.hideKeyboard();
			if (this.getCodeisWaiting) {
				return;
			}
			if (!/^1(3|4|5|6|7|8|9)\d{9}$/.test(this.phoneNumber) && this.phoneNumber != 'admin') {
				uni.showToast({
					title: '请填写正确手机号码',
					icon: 'none'
				});
				return false;
			}
			this.getCodeText = '发送中...';
			this.getCodeisWaiting = true;
			this.getCodeBtnColor = 'rgba(255,255,255,0.5)';
			console.log(this.phoneNumber, '手机号');
			this.$util
				.post('/login/getSms', {
					phone: this.phoneNumber
				})
				.then(res => {
					uni.showToast({
						title: '验证码已发送',
						icon: 'none'
					});
					this.setTimer();
				});
		},
		setTimer() {
			let holdTime = 60;
			this.getCodeText = '重新获取(60)';
			this.Timer = setInterval(() => {
				if (holdTime <= 0) {
					this.getCodeisWaiting = false;
					this.getCodeBtnColor = '#ffffff';
					this.getCodeText = '获取验证码';
					clearInterval(this.Timer);
					return;
				}
				this.getCodeText = '重新获取(' + holdTime + ')';
				holdTime--;
			}, 1000);
		},
		// 登录
		doReg() {

			uni.hideKeyboard();
			//模板示例部分验证规则
			if (!/^1(3|4|5|6|7|8|9)\d{9}$/.test(this.phoneNumber) && this.phoneNumber != 'admin') {
				uni.showToast({
					title: '请填写正确手机号码',
					icon: 'none'
				});
				return false;
			}
			//示例验证码，实际使用中应为请求服务器比对验证码是否正确。
			if (this.code == '') {
				uni.showToast({
					title: '验证码不正确',
					icon: 'none'
				});
				return false;
			}
			uni.showLoading({
				title: '提交中...'
			});


			this.$util.post('/login/smsLogin', {
					phone: this.phoneNumber,
					code: this.code, //驗證碼
					recommendCode: this.recommendCode, //推荐嘛
					open_id: this.openId
				})
				.then(res => {
					// let that=this
					console.log(res, 'adminLogin');
					uni.hideLoading()
					if (res.data.status == 500) {
						uni.showToast({
							title: res.data.msg,
							duration: 2000
						});
					}
					if (res.data.status == 200) {
						console.log(res.data.token, '登陆');
						this.login(res.data.token);
						uni.setStorageSync('CODE', res.data.token)

						return this.$util.get('/xf/PersonalDetails?token=' + res.data.token)
					} else {
						uni.navigateTo({
							url: ''
						})
					}
				})
				.then(res => {
					const isShop = res.data.isShop
					this.$store.state.isShop = isShop
					this.$store.commit('setUserInfo',res.data)
					uni.navigateTo({
						url: '/pages/loginBase/chooseIdentity/chooseIdentity'
					})
					setTimeout(() => {
						let CODE = uni.getStorageSync('CODE')
						this.getLocation()
					}, 300);
				})


			/*
				* 
				
			ymPoi0vW8uUeZCn7DQd3HrTOBKMgtEAhbFJc
		
			 
				* 
				* */
		}
	}
};
